import React from "react";
import TextField from "@material-ui/core/TextField";
import EditIcon from '@material-ui/icons/Edit';
import EditOutlinedIcon from '@mui/icons-material/EditOutlined';
import DeleteForeverOutlinedIcon from '@mui/icons-material/DeleteForeverOutlined';
import VisibilityRoundedIcon from '@mui/icons-material/VisibilityRounded';
import DeleteIcon from '@material-ui/icons/Delete';
import Button from '@material-ui/core/Button';


export const renderEditableCell = (value, tableMeta, updateValue, rows) => {

  return (
    <div>
      <TextField
        style={{ width: "32%" }}
        id="standard-basic"
        defaultValue={value}
        onChange={(e) => {
          updateValue(e.target.value);
          tableMeta.rowData[1] = e.target.value;
          rows[tableMeta.rowIndex].count = e.target.value;
        }}
      />
    </div>
  );
};
export const columns = (handleEdit) => [
  {
    name: "NO",
    label: "##",
    options: {
      filter: false,
      sort: true,
    }
  },
  {
    name: "numero",
    label: "Numero",
    options: {
      filter: false,
      sort: true,
    }
  },
  {
    name: "nome",
    label: "Nome Completo",
    options: {
      filter: false,
      sort: true,
    }
  },
  {
    name: 'sexo',
    label: 'Genero',
    options: {
      filter: true,
      sort: false
    }
  },
  {
    name: "curso",
    label: "Curso",
  },
  {
    name: "dataInscricao",
    label: "Data de Inscriçao",
    options: {
      filter: true,
      sort: true
    }

  },
  {
    name: "usuario",
    label: "Usuario",
  },
  {
    name: 'Ações',
    options: {
      filter: false,
      sort: false,
      empty: true,
      customBodyRender: (value, tableMeta, updateValue) => {
        const rowIndex = tableMeta.rowIndex;
        const rowId = tableMeta.tableData[rowIndex].id; 
        return (
          <div>
          <EditOutlinedIcon onClick={() => handleEdit(rowId)}>Editar</EditOutlinedIcon>
          <VisibilityRoundedIcon onClick={() => handleEdit(rowId)}>Ver</VisibilityRoundedIcon>
          <DeleteForeverOutlinedIcon onClick={() => handleEdit(rowId)}>Apagar</DeleteForeverOutlinedIcon>
          </div>
        );
      }
    }
  }

];


export const errorCols = [
  {
    name: "errorCode",
    label: "Error Code"
  },
  {
    name: "errorMessage",
    label: "Error Message"
  }
];
export const errorOptions = {
  responsive: "standard",
  selectableRows: "none",
  download: false,
  filter: false,
  print: false,
  search: false,
  viewColumns: false
};
