import MUIDataTable from "mui-datatables";
import React, { useState, useEffect } from "react";
import { columns } from "./data_header_table";
import { createMuiTheme, MuiThemeProvider } from "@material-ui/core/styles";
import { requesicoes } from "../../../utils/services";
import { Fab } from "@material-ui/core";
import { useHistory } from 'react-router-dom';
import Swal from "sweetalert2";
import './stly.css';
import { NavLink } from "react-router-dom/cjs/react-router-dom";
import { MoonLoader } from 'react-spinners'; // Importe o componente BounceLoader



let estudantes = []
let inscritos = []
let listaEstudantes = []
export default function MuiDatatableExample() {

  const history = useHistory();
  const [, setSelectedData] = useState(null);
  const [lastClickTime, setLastClickTime] = useState(0);
  const [, setTo] = useState('');
  const [, setRedirect] = useState(false);
  const [updateTable, setUpdateTable] = useState(false);
  const [updateDom, setUpdateDom] = useState(false);
  const [isBtnHabilitado, setIsBtnHabilitado] = useState(false);
  const [carregando, setCarregando] = useState(true);
  const [loading, setLoading] = useState(false);


  //const [, setRedirect] = useState(false);



  const getMuiTheme = () =>
    createMuiTheme({
      overrides: {
        MUIDataTableHeadCell: {
          fixedHeader: { backgroundColor: "darkblue", color: "white" }
        },
        MUIDataTableSelectCell: {
          headerCell: { backgroundColor: "darkblue" },
          checkboxRoot: { color: "white", filter: "brightness(0.5)" },
          expandDisabled: {
            // Soft hide the button.
            visibility: "hidden"
          }
        }
      }
    });



  useEffect(() => {
    init();

    setTimeout(() => {
      setCarregando(false);
    }, 2000);
  }, [updateTable])

  async function init() {
    setCarregando(true);

    listaEstudantes = await Promise.resolve(requesicoes().buscarInscritos('?campos=nome,numero,id,apelido,sexo,dataInscricao,dataMatricula,curso.nome'));
    inscritos = listaEstudantes.data.filter(e => e.dataMatricula === null)
    estudantes = inscritos.map((x, i) => {
      const dataInscricao = new Date(x.dataInscricao);
      const dataFormatada = `${dataInscricao.getDate()}/${dataInscricao.getMonth() + 1}/${dataInscricao.getFullYear()} ${dataInscricao.getHours()}:${dataInscricao.getMinutes()}:${dataInscricao.getSeconds()}`;

      return {
        ...x,
        NO: i + 1,
        numero: x.numero === null ? "Pendente" : x.numero,
        nome: x.nome + ' ' + x.apelido,
        sexo: x.sexo == 'Femenino' ? 'F' : 'M',
        id: x.id,
        dataInscricao: dataFormatada,
        curso: x.curso.nome
      }
    });
    const todosTemNumeroNull = estudantes.every(e => e.numero === "Pendente");

    setIsBtnHabilitado(!todosTemNumeroNull);

    setCarregando(false);

    setUpdateDom(!updateDom);

  }

  const handleEdit = (id) => {
    history.push(`/student/inscricao_edit/${id}`);
  };

  const handleRowDoubleClick = (rowData, rowMeta) => {
    const dataIndex = rowMeta.rowIndex;
    const selectedRow = estudantes[dataIndex];
    setSelectedData(selectedRow);
    const id = selectedRow.id;
    //console.log('Id do cadidato ',id)
    //const encryptedId = encryptParam(id);
    //console.log('Id incripitado, ',encryptedId)
    history.push(`/student/detalhes/${id}`);

    //history.push(`/student/detalhes/${encryptedId}`);
  };

  const handleCellClick = (rowIndex, columnIndex) => {

    const currentTime = new Date().getTime();
    const timeDiff = currentTime - lastClickTime;

    // Verifique se o tempo entre os cliques é menor que 300ms
    if (timeDiff < 300) {
      handleRowDoubleClick(rowIndex, columnIndex);
    } else {
      setLastClickTime(currentTime);
    }
  };


  async function handleMatricular() {
    try {
      setLoading(true); // Ativa o spinner de carga
      await Promise.resolve(requesicoes().matricular());
      Swal.fire(
        'Exito!',
        'Matricula realizada com sucesso!',
        'success'
      )
      setTo('/siguam/student/list');
      setRedirect(true);
      setUpdateTable(true);
    } catch (e) {
      Swal.fire({
        icon: 'error',
        title: 'Oops...',
        text: e.response.data.message,
      })
      setUpdateTable(true);
    }finally {
      setTimeout(() => {
      setLoading(false); // Desativa o spinner de carga após a promise ser resolvida
      }, 2000);
  }

  }
  async function handleCriarCodigo() {
    try {
      await Promise.resolve(requesicoes().criarCodigo());
      Swal.fire(
        'Exito!',
        'Numeros Criado com Sucesso!',
        'success'
      )
      setTo('/siguam/inscrito/list');
      setRedirect(true);
      setUpdateTable(true);
    } catch (e) {
      Swal.fire({
        icon: 'error',
        title: 'Oops...',
        text: e.response.data.message,
      })
      setUpdateTable(true);
    }
  }
  // if (carregando) return 'Carregando';

  return (
    <div >
       {loading &&
        <div >
          <MoonLoader color="#36d7b7" />
        </div>
      }
      {carregando ? (
        <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', width: '100%', marginTop: '20%' }}>
          <MoonLoader color="#36d7b7" />
        </div>

      ) : (
        <MuiThemeProvider theme={getMuiTheme()}>
          <MUIDataTable
            title={'Lista de Estudantes'}
            columns={columns(handleEdit)}
            data={estudantes}
            options={{
              onRowClick: handleCellClick
            }}
          />
        </MuiThemeProvider>
      )}
     
      {isBtnHabilitado ? (
        <Fab onClick={handleMatricular} variant="extended" size="medium"
          style={{
            position: 'fixed',
            bottom: '60px', zIndex: 500,
            right: '15px'
          }} color="primary">Matricular Todos</Fab>
      ) : isBtnHabilitado && estudantes.length == 0 ? (
        <Fab onClick={handleCriarCodigo} variant="extended" size="medium"
          style={{
            position: 'fixed',
            bottom: '60px', zIndex: 500,
            right: '15px'
          }} color="primary">Criar Código</Fab>
      ) : <Fab variant="extended" size="medium" to="/carregar/inscrito" component={NavLink}
        style={{
          position: 'fixed',
          bottom: '60px', zIndex: 500,
          right: '15px'
        }} color="primary">Carregar Estudantes</Fab>}
    </div>
  );
}
